@import url(https://fonts.googleapis.com/css?family=Montserrat:700,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}@-webkit-keyframes color-me-in{0%{background:white}50%{background:#6e5f61}100%{background:#282c34}}@keyframes color-me-in{0%{background:white}50%{background:#6e5f61}100%{background:#282c34}}.App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white;-webkit-animation:color-me-in 5s;animation:color-me-in 5s;background:#282c34}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.parent{display:grid;place-items:center;background:lightblue;width:500px;height:500px;resize:both;overflow:auto}.child{padding:0.5rem;border-radius:10px;border:1px solid red;background:lightpink;font-size:2rem;text-align:center}body{font-family:system-ui, serif}button{border:5px solid transparent;background:#AED6D1;color:#55706D;border-radius:40px;padding:15px 30px;overflow:hidden;width:200px;transition:all        1.2s, border     0.5s 1.2s, box-shadow 0.3s 1.5s;white-space:nowrap;text-indent:10px;font-weight:bold}button span{display:inline-block;transform:translateX(300px);font-weight:normal;opacity:0;transition:opacity 0.1s 0.5s, transform 0.4s 0.5s}button:hover{cursor:pointer;text-indent:0;background:#55706D;color:#FFE8A3;width:300px;border:10px solid #8DCCC4;box-shadow:3px 3px 2px rgba(0,0,0,0.15)}button:hover span{transform:translateX(0);opacity:1}.Footer{position:absolute;left:0;bottom:0;right:0;padding-bottom:30px;color:white}.Footer a{color:#FFF;text-decoration:none}.Footer a:hover{text-decoration:underline}.stocklink{color:white}.stocklink a{color:#FFF;text-decoration:none}.stocklink a:hover{text-decoration:underline}

